<template>
	<div class="container">
		<Header @toggle-add-task="toggleAddTask"
		title="Task Tracker"
		:showAddTask="showAddTask" />
		<div v-show="showAddTask">
			<AddTask @add-task="addTask" />
		</div>
		<p id="no-tasks" v-show="showNoTask">There are no tasks here...</p>
		<Tasks @toggle-rem="toggleRem" @delete-task="deleteTask" :tasks="tasks" />
	</div>
</template>

<script>
import Header from './components/Header';
import Tasks from './components/Tasks';
import AddTask from './components/AddTask';
export default {
	name: 'App',
	components: {
		Header,
		Tasks,
		AddTask,
	},
	data () {
		return {
			tasks: [],
			showAddTask: false,
			showNoTask: true,
		}
	},
	methods: {
		toggleAddTask() {
			this.showAddTask = !this.showAddTask;
		},
		addTask(task) {
			this.tasks = [...this.tasks, task];
			this.showNoTask = !this.showNoTask;
		},
		deleteTask(id) {
			let curT = this.tasks.filter((task) => task.id == id)[0].text;
			if (confirm("Are you sure you want to delete '" + curT + "' from your task list?")) {
				this.tasks = this.tasks.filter((task) => task.id !== id);
			}
			if (this.tasks.length == 0) {
				this.showNoTask = !this.showNoTask;
			}
		},
		toggleRem(id) {
			this.tasks = this.tasks.map((task) => task.id === id
			? {...task, reminder: !task.reminder} : task)
		}
	},
	created() {
		this.tasks = []
	},
};
</script>

