<template>
	<form @submit="onSubmit" class="add-form">
		<div class="form-control">
			<label>Task <span class="req">*required</span></label>
			<transition
				enter-active-class="animate__animated animate__headShake"
			>
				<input type="text" :class="req" v-model="text" name="text" placeholder="Add Task" />
			</transition>
		</div>
		<div class="form-control">
			<label>Day & Time</label>
			<input
				type="text"
				v-model="day"
				name="day"
				placeholder="Add Day & Time"
			/>
		</div>
		<div class="form-control form-control-check">
			<label>Set Reminder</label>
			<input type="checkbox" v-model="reminder" name="reminder" />
		</div>

		<input type="submit" value="Save Task" class="btn btn-block" />
	</form>
</template>

<script>
export default {
	name: 'AddTask',
	data() {
		return {
			text: '',
			day: '',
			reminder: false,
			req: "no-req",
		};
	},
	methods: {
		onSubmit(e) {
			e.preventDefault();
			if (this.text) {
				let newTask = {
					id: Math.floor(Math.random() * 100000),
					text: this.text,
					day: this.day,
					reminder: this.reminder,
				};
				if (newTask.day == '') {
					newTask.day = 'Today at some point?'
				}
				this.$emit('add-task', newTask);
				this.text = '';
				this.day = '';
				this.reminder = false;
				this.req = 'no-req';
			} else {
				this.req = 'req-err';
			}
		},
	},
};
</script>


